import axios, { AxiosRequestConfig } from "axios";
import { createSearchParams } from "libs/util";

const API_URL = "https://opentdb.com/api.php";

const instance = axios.create({ baseURL: API_URL });

interface CreateRequestOptions {
  method: AxiosRequestConfig["method"];
  endpoint: string;
  params?: {
    [key: string]: string | number | Array<string> | Array<number> | undefined;
  };
}

export interface RequestFunction<Arguments = void, Response = void> {
  (args: Arguments): Promise<Response>;
}

export const createRequest = async ({
  method,
  endpoint,
  params = undefined,
}: CreateRequestOptions) => {
  try {
    const { data } = await instance({
      method,
      url: `${endpoint}${createSearchParams(params)}`,
    });

    return data;
  } catch (e) {
    throw e;
  }
};
