import { createRequest, RequestFunction } from "./instance";

export const getQuestions: RequestFunction<
  { amount: number; category: number; difficulty: string; type: string },
  {
    results: Array<{
      category: string;
      question: string;
      correct_answer: string;
      incorrect_answers: Array<string>;
    }>;
  }
> = (params) => {
  return createRequest({
    method: "get",
    endpoint: "/api.php",
    params: { ...params },
  });
};
