import _ from "lodash";

export const convertSpecialSymbol = (string: string) => {
  if (string.includes("&")) {
    const specialSymbol = string.slice(
      string.indexOf("&"),
      string.indexOf(";") + 1
    );

    switch (specialSymbol) {
      case "&#039;":
        return string.replaceAll(specialSymbol, "'");
      case "&amp;":
        return string.replaceAll(specialSymbol, "&");
      default:
        return string.replaceAll(specialSymbol, '"');
    }
  }

  return string;
};

export function createSearchParams(
  obj: {
    [key: string]: string | Array<string> | number | Array<number> | undefined;
  } = {},
  base?: string
) {
  const result = _.reduce(
    obj,
    (searchParams, value, key) => {
      if (value instanceof Array) {
        searchParams.delete(key);
        _.forEach(value, (item) => searchParams.append(key, item.toString()));
      } else if (value !== undefined && value !== "") {
        searchParams.set(key, value.toString());
      } else {
        searchParams.delete(key);
      }

      return searchParams;
    },
    new URLSearchParams(base)
  ).toString();

  return result === "" ? "" : `?${result}`;
}
