/** @jsxImportSource @emotion/react */

import React from "react";
import { css } from "@emotion/react";

import Button from "components/common/Button";
import TitleComp from "components/common/Title";

interface ResultCompPropTypes {
  startTime: number;
  correctCount: number;
  incorrectCount: number;
  handleReturnToFirstQuestion: () => void;
}

function ResultComp({
  startTime,
  correctCount,
  incorrectCount,
  handleReturnToFirstQuestion,
}: ResultCompPropTypes) {
  const currentTime = Date.now();

  return (
    <>
      <TitleComp title="Trivia Contest Result" titleStyle={title} />
      <div css={result}>
        <p>  : {correctCount}</p>
        <p> : {incorrectCount}</p>
        <div>
          <p> : {Math.round((currentTime - startTime) / 1000)}</p>
        </div>
      </div>
      <div css={buttonWrapper}>
        <Button onClick={handleReturnToFirstQuestion}>  </Button>
        {/* <Button> </Button> */}
      </div>
    </>
  );
}

export default ResultComp;

const title = css`
  margin-bottom: 3rem;
  font-size: 3rem;
  font-weight: 800;
`;

const result = css`
  text-align: center;

  p {
    margin-bottom: 2rem;
    font-size: 1.5rem;
  }
`;

const buttonWrapper = css`
  min-width: 12rem;

  button {
    margin-top: 1rem;
  }
`;
