/** @jsxImportSource @emotion/react */

import React, { useCallback, useState, useRef } from "react";
import { css } from "@emotion/react";

import ResultComp from "./Result";
import AnswerButtonComp from "./AnswerButton";
import Button from "components/common/Button";
import TitleComp from "components/common/Title";
import ControlVisible from "components/common/ControlVisible";

import { convertSpecialSymbol } from "libs/util";
interface QuestionCompPropTypes {
  startTime: React.MutableRefObject<number>;
  questions: Array<{
    question: string;
    correctAnswer: string;
    answers: Array<string>;
  }>;
}

function QuestionComp({ startTime, questions }: QuestionCompPropTypes) {
  const correctCount = useRef(0);
  const incorrectCount = useRef(0);
  const [isResult, setResult] = useState(false);
  const [isDisable, setDisable] = useState(false);
  const [isCorrect, setCorrect] = useState(false);
  const [isButtonClicked, setButtonClicked] = useState(false);
  const [questionNumber, setQuestionNumber] = useState(0);
  const [selectedAnswer, setSelectetdAnswer] = useState<string>("");

  const { question, answers, correctAnswer } = questions[questionNumber];
  const isLast = questionNumber === questions.length - 1;

  const handleButtonDisable = useCallback(() => {
    setDisable((prev) => !prev);
  }, []);

  const handleGetAnswer = useCallback(
    (e: React.MouseEvent<HTMLButtonElement>) => {
      const target = e.target as HTMLButtonElement;

      handleButtonDisable();
      setSelectetdAnswer(target.id);
      setButtonClicked(true);

      if (correctAnswer === target.value && correctCount) {
        setCorrect(true);
        return (correctCount.current += 1);
      }

      setCorrect(false);

      if (incorrectCount) {
        return (incorrectCount.current += 1);
      }
    },
    [correctAnswer, handleButtonDisable]
  );

  const handleNextPage = () => {
    if (isLast) {
      return setResult(true);
    }

    handleButtonDisable();
    setButtonClicked(false);
    setSelectetdAnswer("");
    setQuestionNumber((prev) => {
      return (prev += 1);
    });
  };

  const handleReturnToFirstQuestion = useCallback(() => {
    setQuestionNumber(0);
    setResult(false);
    setCorrect(false);
    setDisable(false);
    setButtonClicked(false);
    setSelectetdAnswer("");
    correctCount.current = 0;
    incorrectCount.current = 0;
    startTime.current = Date.now();
  }, [startTime]);

  return (
    <section css={wrapper}>
      {isResult ? (
        <ResultComp
          startTime={startTime.current}
          correctCount={correctCount.current}
          incorrectCount={incorrectCount.current}
          handleReturnToFirstQuestion={handleReturnToFirstQuestion}
        />
      ) : (
        <>
          <TitleComp titleStyle={titleStyle}>
            <p>{`Q${questionNumber + 1}. `}</p>
            <p>{`${convertSpecialSymbol(question)}`}</p>
          </TitleComp>

          <AnswerButtonComp
            answers={answers}
            selectedAnswer={selectedAnswer}
            convertSpecialSymbol={convertSpecialSymbol}
            handleGetAnswer={handleGetAnswer}
            isDisable={isDisable}
          />

          <ControlVisible isVisible={isButtonClicked} style={correctText}>
            {isCorrect ? (
              <p>.</p>
            ) : (
              <p className="incorrect">
                 {questions[questionNumber].correctAnswer}.
              </p>
            )}
          </ControlVisible>

          <ControlVisible style={nextButtonStyle} isVisible={isButtonClicked}>
            <Button
              buttonTitle={isLast ? " " : " "}
              onClick={handleNextPage}
            />
          </ControlVisible>
        </>
      )}
    </section>
  );
}

export default QuestionComp;

const wrapper = css`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

const titleStyle = css`
  display: flex;
  justify-content: center;

  p {
    margin-right: 0.5rem;
    line-height: 1.5;

    &:last-of-type {
      margin-right: 0;
    }
  }
`;

const correctText = css`
  margin-bottom: 2rem;
  font-weight: 600;

  .incorrect {
    color: red;
  }
`;

const nextButtonStyle = css`
  width: 20%;
`;
