/** @jsxImportSource @emotion/react */

import React from "react";
import { css } from "@emotion/react";

import Button from "components/common/Button";

interface AnswerButtonCompProTypes {
  answers: Array<string>;
  isDisable: boolean;
  selectedAnswer: string;
  handleGetAnswer: (e: React.MouseEvent<HTMLButtonElement>) => void;
  convertSpecialSymbol: (value: string) => string;
}

function AnswerButtonComp({
  answers,
  isDisable,
  selectedAnswer,
  handleGetAnswer,
  convertSpecialSymbol,
}: AnswerButtonCompProTypes) {
  return (
    <article css={answerButtonWrapper}>
      {answers.map((answer, index) => {
        return (
          <Button
            key={answer}
            id={`answer button ${index}`}
            testId={`answer button ${index}`}
            className={
              selectedAnswer.includes(`${index}`) ? "selected" : undefined
            }
            value={convertSpecialSymbol(answer)}
            onClick={handleGetAnswer}
            disabled={isDisable}
            buttonTitle={convertSpecialSymbol(answer)}
          />
        );
      })}
    </article>
  );
}

export default AnswerButtonComp;

const answerButtonWrapper = css`
  display: flex;
  width: 70%;

  button {
    margin: 1rem;
    margin-bottom: 2rem;
    padding: 1rem;
    line-height: 1.5;

    &.selected {
      color: white;
      background-color: #828282;
    }
  }
`;
