import React, { useCallback, useState, useRef } from "react";

import { getQuestions } from "apis/trivia";

import MainComp from "components/Trivia/Main";
import QuestionComp from "components/Trivia/Question";

interface QuestionsType {
  answers: Array<string>;
  question: string;
  correctAnswer: string;
}

const initialValue = {
  answers: [],
  question: "",
  correctAnswer: "",
};

function TriviaContainer() {
  const startTime = useRef(0);
  const [questions, setQuestions] = useState<Array<QuestionsType>>([
    initialValue,
  ]);

  const handleGetQuestion = useCallback(async () => {
    const params = {
      amount: 10,
      category: 21,
      difficulty: "easy",
      type: "multiple",
    };

    const { results } = await getQuestions(params);

    startTime.current = Date.now();

    setQuestions(() => {
      return results.map(({ question, correct_answer, incorrect_answers }) => {
        return {
          question,
          correctAnswer: correct_answer,
          answers: [...incorrect_answers, correct_answer],
        };
      });
    });
  }, []);

  return (
    <>
      {questions[0].question ? (
        <QuestionComp questions={questions} startTime={startTime} />
      ) : (
        <MainComp handleGetQuestion={handleGetQuestion} />
      )}
    </>
  );
}

export default TriviaContainer;
